@import url("https://fonts.googleapis.com/css?family=Raleway&display=swap");
.Home {
  color: white;
  margin-left: 5vw;
  font-family: 'Raleway', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%; }

.section-label {
  position: absolute;
  right: 0;
  font-size: 3rem;
  margin: 25px;
  align-self: flex-start; }

.home-content {
  display: flex;
  justify-content: space-around;
  width: 60%;
  line-height: 70px; }

.name {
  font-size: 3.2rem; }

.intro-hold {
  display: flex;
  flex-direction: column; }

.intro-head {
  font-size: 2.5rem;
  padding-right: 10px; }

.intro {
  font-size: 2.5rem; }

.titles-hold {
  display: flex; }

.photo-hold {
  width: 300px;
  height: 300px;
  border-radius: 150px;
  background: white; }
